@media (min-width: 768px) {
  .producto-grid {
    display: grid;
    gap: 2;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 2rem;
  }
}
.producto-grid .producto-grid-imagen {
  margin-right: 2rem;
  background-color: #f1f1f6;
  padding: 0.2rem;
}
.producto-grid .producto-grid-imagen img {
  height: 55rem;
}
@media (min-width: 768px) {
  .producto-grid .producto-grid-info {
    margin-top: 2rem;
  }
}
.producto-grid .producto-grid-info .producto-detalle, .producto-grid .producto-grid-info label {
  font-family: "Kalnia", sans-serif;
  font-weight: bold;
  color: #79451d;
  text-align: center;
  font-size: 2.5rem;
}
.producto-grid .producto-grid-info .producto-detalle .producto-detalle-span, .producto-grid .producto-grid-info label .producto-detalle-span {
  font-family: "Lato", sans-serif;
  font-weight: normal;
  color: #2f2c2b;
}
.producto-grid .producto-grid-info .producto-detalle input, .producto-grid .producto-grid-info label input {
  width: 60%;
  background-color: #FFFFFF;
  border: 1px solid #2f2c2b;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.no-webp .inicio {
