  justify-content: space-between;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 1rem;
  }
}
.barra .a-header {
  font-size: 5rem;
  font-family: "Kalnia", sans-serif;
  text-align: center;
  font-weight: 300;
  color: #000000;
